.carousel .slide {
    background: transparent;
}

.carousel .control-dots {
    padding: 0;
}

.carousel .control-dots .dot {
    transition: background 0.25s ease-in;
    background: #D8D8D8;
    border-radius: 50%;
    width: 10px;
    box-shadow: none;
    opacity: 1;
    height: 10px;
    cursor: pointer;
    display: inline-block;
    margin: 0 8px;
}

.carousel .control-dots .dot.selected {
    background: #9C9C9C;
}
